@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}body{color:var(--white);font-family:Poppins, sans-serif;-webkit-user-select:none;-ms-user-select:none;user-select:none;transition:0.2s}body ul,body li,body a{text-decoration:none;color:inherit;list-style:none}body input,body textarea{font-family:inherit;outline:none;border:1px solid rgba(51,51,51,0.3)}::-webkit-scrollbar{width:12px;background-color:#2d2e30}::-webkit-scrollbar-button{display:none}::-webkit-scrollbar-thumb{border:2px solid #2d2e30;margin:0 auto;background-color:#5f6368;border-radius:5px}.row{flex-grow:1}.row.screen{display:flex;align-items:flex-end;justify-content:flex-end;flex-direction:column}.row .output{margin:0;text-align:right;font-size:3rem}.row .output__mini{text-align:right;color:#fffa}.container-fuild{width:100vw;height:100vh;display:flex;flex-direction:column;grid-gap:1.5rem;gap:1.5rem}.container-fuild .row.number-pad{--gap-number: 0.5rem;grid-gap:var(--gap-number);gap:var(--gap-number);flex-grow:6}.container-fuild .row.number-pad .row{display:flex;grid-gap:var(--gap-number);gap:var(--gap-number);height:calc(100% / 5)}.container-fuild .row.number-pad .row .col{width:25%;text-align:center;border-radius:10px;background-color:#374353ee;padding:1rem;cursor:pointer;color:#fffa;box-shadow:-2px -2px 2px #fffa;font-size:2rem;display:flex;justify-content:center;align-items:center}.container-fuild .row.number-pad .row .col:hover{color:#ffff;background-color:#374353ff;font-weight:bolder}.container-fuild .row.number-pad .row .col.disable{pointer-events:none;box-shadow:-2px -2px 2px #f00a}.container-fuild .row.number-pad .row .col.active{pointer-events:none;box-shadow:-2px -2px 2px #0f0a}*{color:black}body{background-color:#374353}

